@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
  box-sizing: border-box;
}

body{
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

.App{
  background: #1D2671;
  color: whitesmoke;
  transition: all 0.25s linear
}

.App.lightMode{
  background: white;
  color: black;
}

.header-section{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 15px;
  border-bottom: 1px solid #3c4380;
}


.logo{
  width: 30px;
  height: 30px;
  margin-right: 8px;
}

.logo-name{
  font-size: 16px;
}

.toggle-flex{
  margin-left: auto;
  /* outline: 1px solid black; */
}


.input-section{
  /* outline: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 130px;
}

.input-section.toggle-search{
  width: 130px;
  margin-left: auto;
  outline: 1px solid rgb(58, 79, 156);
  padding: 2px;
  border-radius: 3px;
  box-shadow: 0px 0px 10px 5px rgb(28, 28, 126);
}

.input-section.toggle-search.lightMode{
  box-shadow: 0px 0px 10px 5px rgb(238, 238, 238);
  outline: 1px solid rgb(138, 138, 138);
}


.fas{
  margin-right: 5px;
  font-size: 18px;
}

.fa-search{
  font-size: 15px;
}

.fa-search.search-icon{
  color: #adadad;
  margin-left: 5px;
}

.fas:hover{
  cursor: pointer;
}

.search{
  background: transparent;
  border: none;
  width: 110px;
  color: white;
}

.search.lightMode{
  color: black;
}

.search:focus{
  outline: none;
}

.filter-section{
  padding: 15px;
}

.sort-filter{
  box-shadow: 0px 0px 25px 3px rgb(21, 5, 94);
  padding: 10px;
  background: #28328d;
  border: none;
  color: white;
  border-radius: 2px;
  position: relative;
  margin-top: 20px;
}

.sort-filter.lightMode{
  box-shadow: 0px 0px 25px 3px rgb(231, 231, 231);
  background: rgb(224, 224, 224);
  color: black;
}

.sort-filter:hover{
  cursor: pointer;
}

.sort-filter:focus{
  outline: none;
}


.movies{
  /* outline: 1px solid black; */
  margin: auto;
  padding: 20px 25px;
}

.movies-list{
  /* outline: 1px solid whitesmoke; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 25px;
  background: rgba(40, 50, 141,0.5);
  border-radius: 5px;
  backdrop-filter: blur(10px);
  box-shadow: 0px 0px 25px 7px rgb(21, 5, 94);
  padding-bottom: 15px;
}

.movies-list.lightMode{
  background: gainsboro;
  box-shadow: 0px 0px 25px 7px rgb(175, 175, 175);
}

.release-date, .rating{
  margin: 0;
  padding: 0;
  font-size: 15px;
}

.rate{
  background: rgb(12, 12, 105);
  padding: 1px 5px;
  box-shadow: 0px 0px 5px 2px inset rgb(9, 9, 71);
  border-radius: 3px;
}

.rate.lightMode{
  background: rgb(224, 222, 222);
  box-shadow: 0px 0px 5px 2px inset rgb(180, 180, 180);
  color: rgb(0, 81, 255);

}

.movie-name{
  margin: 0;
  padding: 0;
  margin-top: 30px;
  color: rgb(255, 251, 0);
}

.movie-name.lightMode{
  color: rgb(0, 81, 255);
}


.poster{
  width: 100%;
  max-height: 50vh;
  border-radius: 5px 5px 0 0 ;
  transition: all 0.25s ease;
  box-shadow: 5px 5px 25px 7px rgb(21, 5, 94);
}

.poster.lightMode{
  box-shadow: 5px 5px 25px 7px rgb(211, 211, 211);

}

.poster:hover{
  cursor: pointer;
  transform: scale(1.02);
}


/* movies details section */

.movies-details{
  padding: 0 20px;
}

.status{
  position: absolute;
  z-index: 2;
  outline: 1px solid black;
  margin:2px 0 0 2px ;
  border-radius: 2px;
}

.status p{
  margin: 0;
  padding: 0;
  padding: 0 3px;
  font-size: 12px;
}

.movie-details-section{
  /* outline: 1px solid red; */
  min-height: 100vh;
  position: relative;
  padding-bottom: 40px;
  margin-top: 10px;
}

.info-title{
  position: absolute;
  z-index: 2;
  font-size: 25px;
  margin: 0 auto;
  left : 0;
  right: 0;
  margin-top: 50px;
  text-align: center;
  
  text-shadow: 1px 4px 10px black;
}

.info-title.lightMode{
  color: white;
}

.backdrop{
  width: 100%;
  filter: blur(1px);
  /* position: absolute;
  margin: 0; */
}

.overview{
  /* padding: 0 15px; */
}

.genres{
  /* outline: 1px solid green; */
  
}

.genres h4{
  margin-bottom: 0;
  /* outline: 1px solid white; */
}

.genre-info{
  outline: 1p solid black;
  display: grid;
  grid-template-columns: repeat(3,auto);
  font-size: 14px;
}

.genre-name{
  /* outline: 1px solid black; */
}

.language, .runtime, .release-date{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* outline: 1px solid black; */
  height: 50px;
}

.language h4, .runtime h4, .release-date h4{
  margin-right: 10px;
}

.language p{
  text-transform: capitalize;
}


.homepage{
  /* outline: 1px solid red; */
  margin-top: 0;
}

.homepage h4{
  margin-bottom: 10px;
}

.homepage a{
  word-wrap: break-word;
  color: white;
  text-decoration: none;
  font-size: 15px;
}

.homepage.lightMode a{
  color: rgb(0, 102, 255);
}

.homepage a:hover{
  color: gainsboro;
}

.homepage.lightMode a:hover{
  color: blue;
}

.productions h4{
  margin-bottom: 0;
}

.comp-flex{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 50px;
}

.comp-flex p{
  font-size: 14px;
}

.prod-logo{
  width: 70px;
  margin-right: 10px;
}

/* reviews section */
.reviews-section{
  /* outline: 1px solid black; */
  padding: 0 15px;
}

.details-links{
  display: grid;
  grid-template-columns: repeat(3,1fr);
}

.back-btn, .review-btn, .videos-btn, .recommended-btn{
  border: none;
  padding: 8px 20px; 
  background: #28328d;
  margin: 10px 0;
  color: white;
  font-family: 'Poppins', sans-serif;
  border-radius: 3px;
  box-shadow: 0 0 15px 5px #1e2569;
  transition: all 0.25s ease;
}

.back-btn.lightMode, .review-btn.lightMode, .videos-btn.lightMode, .recommended-btn.lightMode{
  background: gainsboro;
  color: black;
  box-shadow: 0 0 15px 5px #dfdfdf;
}

.back-btn:hover, .review-btn:hover, .videos-btn:hover, .recommended-btn:hover{
  cursor: pointer;
  background: #303cad;
  transform: scale(1.04);
}

.back-btn.lightMode:hover, .review-btn.lightMode:hover, .videos-btn.lightMode:hover, .recommended-btn.lightMode:hover{
  color: white;
}

.review-btn, .videos-btn, .recommended-btn{
  margin-top: 30px;
  padding: 5px 15px;
}



.profile{
  padding: 10px 0;
  border-bottom: 1px solid gainsboro;
}

.profile p{
  margin: 0;
  padding: 2px;
}

.profile-name{
  margin-top: 5px;
  color: yellow;
}

.profile-rating{
  font-size: 14px;
}

.profile-rating span{
  margin-left: 5px;
}

.content{
  word-wrap: break-word;
  font-size: 15px;
}

.created{
  border-top: 1px solid gainsboro;

}

.created-at{
  font-size: 13px;
}

.video-section{
  padding: 0 10px;
}

.links{
  padding: 5px;
  border-bottom: 1px solid #3c4380;
  margin-top: 20px;
}

.video-link{
  color: white;
  text-decoration: none;
}

.play{
  max-width: 15%;
  max-height: 15%;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20%;
  z-index: 2;
  transition: all 0.25s ease;
}

.play:hover{
  transform: scale(1.05);
}

.thumbnail{
  width: 100%;
  transition: all 0.25s ease;
  box-shadow: 0 0 15px 5px #191e50;
}

.thumbnail:hover{
  transform: scale(1.05);
}

.link-name{
  margin: 0;
  margin-top: 5px;
}

/* similar movies */

.similar-movies{
  padding: 20px 0;
  border-bottom: 1px solid #3c4380;
}


.sm-card{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 10px;
  padding-bottom: 10px;
}

.sm-section{
  /* outline:1px solid red; */
  box-shadow: 0 0 15px 5px #191f55;
  outline: 1px solid #3c4380;
  color: white;
  text-decoration: none;
}

.sm-section.lightMode{
  box-shadow: 0 0 15px 5px #adadad;
  color: #000000;
  outline: none;
}


.sm-poster{
  width: 100%;
  height: 100px;
  box-shadow: 5px 5px 25px 7px rgb(23, 5, 104);
  transition: all 0.35s ease;
}

.sm-poster.lightMode{
  box-shadow: 5px 5px 25px 7px rgb(161, 161, 161);

}

.sm-poster:hover{
  transform: scale(1.05);
}

.sm-info{
  padding: 0 5px;
  text-align: center;
}

.sm-title{
  font-size: 11px;
}
.sm-releasedate, .sm-rating{
  font-size: 10px;
}

/* recommended movies */

.rm-movies{
  padding: 0 15px;
  /* outline: 1px solid red; */
}

.recommended-movies{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 10px;
  /* outline: 1px solid black; */
}

.recommended-section{

  outline: 1px solid #3c4380;
  font-size: 14px;
}

.rm-poster{
  width: 100%;
  box-shadow: 5px 5px 25px 7px rgb(23, 5, 104);
  transition: all 0.35s ease;
}

.rm-poster:hover{
  transform: scale(1.04);
}

.rm-info{
  padding: 10px;
  text-align: center;
}

.rm-release, .rm-rating{
  font-size: 13px;
}

/* keywords */
.keyword{
  /* outline: 1px solid red; */

}

.keywords-grid{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 10px;
  /* outline: 1px solid black; */
  font-size: 13px;
  padding: 0 10px;
}

.keyword-section{
  /* outline: 1px solid white; */
  /* text-align: center; */
}

/* casts section */

.casts{
  
}

.casts-details{
  /* outline: 1px solid black; */
  outline: 1px solid #3c4380;

  font-size: 14px;
  display: grid;
  grid-auto-flow: column;
  gap: 10px;
  /* grid-template-columns: repeat(5,auto); */
  overflow-x: scroll;
  padding: 10px;
}

.casts-details::-webkit-scrollbar, #ddlYears::-webkit-scrollbar{
  width: 10px;
  height: 10px;
  background: transparent;
}

.casts-details::-webkit-scrollbar-track, #ddlYears::-webkit-scrollbar-track {
  background-color: gainsboro;
  border-radius: 10px;
  box-shadow: inset 0 0 5px 2px rgb(180, 180, 180);
}

.casts-details::-webkit-scrollbar-thumb, #ddlYears::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-image: linear-gradient(180deg, #3667d0 0%, #567be0 99%);
  
  background-color:  #567be0;
}


.casts-info{
  /* outline: 1px solid white; */
  padding-bottom: 5px;
}

.casts-info p {
  margin: 0;
  padding: 5px;
}

.character-name{
  font-size: 12px;
}

.cast-image{
  width: 100px;
  height: 100px;
}

/* pagination */

.page-section{
  /* outline: 1px solid red; */
  padding: 0 15px;
}

.page-display{
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 13px;
  margin-top: 50px;
}

.page-display p{
}

.pagination{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  /* outline: 1px solid black; */
  padding: 0;
  /* background: rgba(40, 50, 141,0.5); */
  
}

.page-item{
  list-style: none;
  margin: 0;
  outline: 1px solid rgba(252, 252, 252, 0.466);
  padding: 5px 10px;
  font-size: 14px;
}

.page-item:hover{
  background: rgba(9, 24, 158, 0.945);
}


.page-link:hover{
  cursor: pointer;

}



/* for desktop */

@media(min-width:600px){

  .input-section{
    margin-left: 80%;
  }

  .input-section.toggle-search{
    width: 30%;
  }

  .search{
    width: 100%;
    /* outline: 1px solid black; */
  }

  .movies{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 15px;
    padding: 20px 30px;
  }

  .poster{
    /* min-height: 50vh; */
  }

  .dropdown-section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }

  .page-section{
    display: flex;
    justify-content: center;
    align-items: center;
    
  }

  .pagination{
    /* outline: 1px solid red; */
    width: 50%;
  }

  .page-item{
    padding: 5px 20px;
    font-size: 16px;
  }

  .page-display{
    display: flex;
    justify-content: center;
    align-items: center;
    /* outline: 1px solid red; */
    
  }

  .movies-details{
    padding: 0 30px;
  }

  .info-title{
    text-align: left;
    left: 100px;
    margin-top: 100px;
  }

  .backdrop{
    width: 50%;
    height: 100%;
  }

  .genre-info{
    grid-template-columns: repeat(5,auto);
  }

  .details-links{
    grid-template-columns: repeat(3,auto);
  }

  .back-btn, .review-btn, .videos-btn, .recommended-btn{
    padding: 10px 30px;
  }

  .sm-card{
    grid-template-columns: repeat(5,1fr);
    gap: 15px;
  }

  .sm-poster{
    height: 200px;
  }

  .sm-title{
    font-size: 14px;
  }

  .sm-releasedate, .sm-rating{
    font-size: 12px;
  }

  .video-info{
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3,1fr);
  }

  .play{
   display: none;
  }

  .rm-movies{
    padding: 0 25px;
  }

  .recommended-movies{
    grid-template-columns: repeat(5,1fr);
    gap: 20px;
  }

}